.site-content {
  padding: 8em 8em 4em 8em;
}

.header-nav {
  header {
    background-color: white;
    opacity: 0.69;
    position: fixed;
    z-index: 99;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .site-branding {
      width: 70px;
      height: 60px;
      margin-left: 10em;
    }
    nav {
      display: flex;
      align-items: center;
      margin-right: 100px;
      .menu-main-menu-container {
        ul {
          font-family: $font__header;
          display: flex;
          margin: 0;
          li {
            padding-right: 20px;
            list-style: none;
            a {
              text-transform: uppercase;
              color: $color__green-brand;
              &:visited {
                color: $color__green-brand;
              }
            }
          }
        }
      }
    }
  }
}
.form-button {
  display: flex;
  .search-submit {
    border: none;
    width: 1em;
  }
}

.main-search-form {
  display: flex;
  align-items: center;

  .search-field {
    transition: 0.6s;
    width: 0;
    border: 0;
    padding: 0;
  }

  .search-animation {
    width: 150px;
    padding: 8px 15px;
  }
  .icon-search {
    color: $color__green-brand;
  }
}
